import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxQRCodeModule } from '@techiediaries/ngx-qrcode';
import { Location } from '@angular/common';
import { LoadingController, Platform, ToastController } from '@ionic/angular';
import { ChannelService } from 'src/app/services/channel.service';
import { Channel } from 'src/app/objects/channel.model';
import { MessageService } from 'src/app/services/message.service';  
import { Message } from 'src/app/objects/message.model';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';

@Component({
  selector: 'app-create-qr',
  templateUrl: './create-qr.page.html',
  styleUrls: ['./create-qr.page.scss'], 
})

export class CreateQrPage implements OnInit {
  channel_id  :string
  title       = 'app';
  elementType = 'img';
  channelUrl  = 'Techiediaries';
  channel: Channel;
  messages: Message[];
  channel_is_flash = false;
  show_share_icons:boolean = true;
  
  constructor(public route: ActivatedRoute,
              private platform    : Platform,
              private socialSharing: SocialSharing,
              public location:Location,
              public router:Router,
              private toastController:ToastController,
              private loadingController:LoadingController,
              private channelService:ChannelService,
              private messageService:MessageService,
              @Inject("rootURL") public rootURL:URL,
              private ngxQRCodeModule:NgxQRCodeModule) { }

  ngOnInit() {  
    this.channel_id       = this.route.snapshot.paramMap.get('id') 
    this.getChannelInfo()
    if(this.platform.is('desktop') || this.platform.is('mobileweb')){
      this.show_share_icons = false;
    }
  }

  back(){
    this.location.back();
  }

  async getChannelInfo(){
    const loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration:5000,
    });
    await loading.present();
    
    this.channelService.getChannelInfo(this.channel_id).ref.get().then((snapshot)=>{
      loading.dismiss();
      this.channel    = <Channel>snapshot.data();
      this.channel.id = this.channel_id;
      this.channel_is_flash = this.channel.flash; 
      if(this.channel.flash){
         this.getAllMessages();
      }
    })
  }

  getAllMessages(){
    this.messageService.getAllMessages(this.channel.id).ref.get().then(snapshot=>{ 
      this.messages = snapshot.docs.map((c)=>{ 
        let b   = c.data()
        b.id  = c.id
        return <Message>b
      })
    }) 
  }

  shareWhatsapp(url:string,message?:Message){
    debugger 
    const text    = message?message.text:(this.channel.channel_name + this.channel.description);
    this.socialSharing.shareViaWhatsApp(text,null, url).then((res) => {
      console.log(res)
    }).catch((e) => {
      console.log(e)
    });
  }

  shareInstagram(url:string,message?:Message){
    debugger 
    const text    = message?message.text:(this.channel.channel_name + this.channel.description);
    const image   = "" 
    this.socialSharing.shareViaInstagram(text,image).then((res) => {
      console.log(res)
    }).catch((e) => {
      console.log(e)
    });
  }

  shareFacebook(url:string,message?:Message){
    debugger 
    const text    = message?message.text:(this.channel.channel_name + this.channel.description);
    const image   = "" 
    this.socialSharing.shareViaFacebook(text,image,url).then((res) => {
      console.log(res)
    }).catch((e) => {
      console.log(e)
    });
  }

  async copyToClipBoard(url:string){  
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = url;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    const toast = await this.toastController.create({
      color: 'dark',
      message: "Bağlantı panoya kopyalandı",
      duration: 2000
    });
    toast.present();
  }

}
